<template>
  <div>

    <div class="mb-3 p-3 small">
      <div class="input-group">
        <input type="search" class="form-control" placeholder="جستجو میان ویدئوها"/>
        <div class="input-group-append">
          <input type="button" class="btn btn-primary" value="جستجو"/>
        </div>
      </div>
      <div class="mt-1">
        <b class="font-weight-bolder m-1">مرتب سازی بر اساس:</b>

        <button class="btn btn-sm m-1" :class="sortType === 'alphabet-asc' ? 'btn-info' : 'btn-light'"
        @click="sortType = 'alphabet-asc'">
          <span class="fa fa-sort-alpha-up mr-1"></span>
          <span>نام صعودی</span>
        </button>

        <button class="btn btn-sm m-1" :class="sortType === 'alphabet-desc' ? 'btn-info' : 'btn-light'"
                @click="sortType = 'alphabet-desc'">
          <span class="fa fa-sort-alpha-down mr-1"></span>
          <span>نام نزولی</span>
        </button>

        <button class="btn btn-sm m-1" :class="sortType === 'last-published' ? 'btn-info' : 'btn-light'"
                @click="sortType = 'last-published'">
          <span class="fa fa-sort-numeric-up mr-1"></span>
          <span>تاریخ انتشار</span>
        </button>
      </div>
    </div>

    <div class="row mx-0">

      <router-link class="col-md-6 p-1 un-decorated" v-for="x in 20" :key="x"
                   :to="x| showVideoUrl">

        <div class="d-flex bg-light rounded">
          <div class="video-item-snapshot p-1 align-self-center">
            <img src="../assets/videos/snapshot.jpg"/>
          </div>

          <div class="ml-1 p-1">
            <div class="font-weight-bolder mb-1">
              دیرین دیرین قسمت
              {{ x }}
            </div>
            <p class="small text-muted">
              به نظر شما دولت در آینده برای درآمدزایی بیشتر چه کارهایی می‌کنه؟ الف) ما رو گرو میذاره وام می‌گیره. ب)
              ماهانه از مردم یارانه می‌گیره. ج) ما رو صادر می‌کنه د) مجبورمون می کنه سر چهارراه‌ها و تو مترو نفت خام
              بفروشیم.
            </p>

            <div class="mt-1 text-center">

              <span class="badge badge-info m-1" title="امتیاز">
                <span class="fa fa-hourglass-half mr-1"></span>
                <span>1:19</span>
              </span>

              <span class="badge badge-success m-1" title="امتیاز">
                <span class="fa fa-coins mr-1"></span>
                <span>{{ random(20, 100) }}</span>
              </span>

              <span class="badge badge-secondary m-1" title="این ویدئو قبلا دیده شده است">
                <span class="fa fa-eye"></span>
              </span>

              <span class="badge badge-primary m-1" title="این ویدئو تاکنون دیده نشده است">
                <span class="fa fa-eye-slash"></span>
              </span>

            </div>
          </div>
        </div>

      </router-link>

    </div>

    <div class="my-3">
      <nav aria-label="Page navigation example">
        <ul class="pagination justify-content-center">
          <li class="page-item"><a class="page-link" href="#">قبلی</a></li>
          <li class="page-item active"><a class="page-link" href="#">1</a></li>
          <li class="page-item"><a class="page-link" href="#">2</a></li>
          <li class="page-item"><a class="page-link" href="#">3</a></li>
          <li class="page-item"><a class="page-link" href="#">بعدی</a></li>
        </ul>
      </nav>
    </div>

  </div>
</template>

<script>
import {random} from '../assets/tools/Functions'

export default {
  name: "Videos",
  data: () => ({sortType: 'alphabet-asc'}),
  methods: {
    random: function (min, max) {
      return Math.round(random(min, max));
    }
  },
  filters: {
    showVideoUrl: function (x) {
      return '/viewing-videos/' + btoa(x);
    }
  }
}
</script>

<style scoped>
.video-item-snapshot {
  flex: 30% 0 0;
}
</style>